.wrapping-paper {
  display: flex;
  flex-direction: column;
  margin: 20px;
}

.wrapping-paper img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  margin-bottom: 20px;
}


.wrapping-paper-vert {
  display: flex;
  flex-direction: row-reverse;
  margin: 20px;
  align-items: center;
}

.wrapping-paper-vert img {
  width: 100px;
  height: 100px;
  object-fit: cover;
  margin-bottom: 20px;
}

.product-card {
  display: flex;
  flex-direction: column;
  width: 300px;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
  border-radius: 4px;
  cursor: pointer;
}

.product-card img{
  user-select: none;
  pointer-events: none;
  max-width: 300px;
  object-fit: contain;
}

.product-card div{
  padding: 0px 15px;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
